// You can delete this file if you're not using it

export const wrapRootElement = ({element}) => {
  console.log(Theme)
  return  <Fragment>
            <GlobalStyles/>
            <ThemeProvider theme={Theme}>
              {element}
            </ThemeProvider>
          </Fragment>
}